@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");

body {
	padding: 0;
	margin: 0;
	font-family: "Inter", sans-serif;
}

header {
	text-align: center;
	margin: 0 auto;
	font-size: 16px;
	width: 94%;
	border: 1px solid red;
	padding-top: 5%;
	padding-bottom: 10%;
}

#appTitle {
	color: #3730a3;
	padding-bottom: 5%;
}

#appIntro {
	color: #818cf8;
	padding-left: 1%;
	padding-right: 1%;
}

.textDiv {
	width: 94%;
	margin: 0 auto;
	border: 1px solid blue;
}

#textDivTitle {
	font-size: 14px;
	color: #374151;
	margin-bottom: 2%;
}

#resetSpan {
	float: right;
	font-size: 12px;
	color: #1d4ed8;
	padding-right: 2%;
}

#textAreaDiv {
	margin: 0 auto;
	border: 1px #d1d5db solid;
	border-radius: 4px;
	padding: 1%;
}

#txtArea {
	width: 100%;
	height: 30%;
	font-size: 14px;
	font-family: "Inter", sans-serif;
	color: #6b7280;
	resize: none;
	outline: none;
	border: none;
}

.resultsDiv {
	width: 94%;
	border: 1px solid purple;
}

